
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 16px 12px 16px;
  min-height: 80px;
  background: var(--header-bg);
  border-bottom: 1px solid var(--border-color);

  .header-left {
    display: flex;
    gap: 8px;
    align-items: center;
  }

  .collapse-btn,
  .theme-btn,
  .preset-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    background: var(--button-bg);
    border: 1px solid var(--border-color);
    border-radius: 6px;
    color: var(--text-accent);
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      background: var(--button-hover);
      color: var(--text-primary);
      border-color: var(--border-active);
    }

    i {
      font-size: 12px;
    }
  }

  .location-info {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 6px;
    margin-left: auto;

    .danger-badge {
      font-size: 10px;
      padding: 2px 6px;
      border-radius: 10px;
      color: #fff;
    }

    .location {
      display: flex;
      align-items: center;
      gap: 8px;

      .location-hierarchy {
        display: flex;
        align-items: center;
        gap: 4px;

        .location-parent {
          font-size: 11px;
          opacity: 0.7;
        }

        .location-separator {
          font-size: 11px;
          opacity: 0.5;
        }

        .location-current {
          font-size: 13px;
          font-weight: 500;
        }
      }
    }
  }
}

// 手机端适配
@media screen and (max-width: 480px) {
  .header {
    padding: 16px 12px 10px 12px;
    min-height: 65px;

    .header-left {
      gap: 6px;
    }

    .collapse-btn,
    .theme-btn,
    .preset-btn {
      width: 26px;
      height: 26px;

      i {
        font-size: 11px;
      }
    }

    .location-info {
      .danger-badge {
        font-size: 9px;
        padding: 2px 5px;
      }

      .location {
        gap: 6px;

        .location-hierarchy {
          .location-parent {
            font-size: 10px;
          }

          .location-current {
            font-size: 12px;
          }
        }
      }
    }
  }
}
