
// 状态卡片
.peace-state-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 30px 20px;
  margin-bottom: 20px;
  background: linear-gradient(135deg, rgba(68, 170, 68, 0.1) 0%, rgba(100, 200, 150, 0.08) 100%);
  border: 1px solid rgba(68, 170, 68, 0.3);
  border-radius: 16px;
  position: relative;
  overflow: hidden;
  transition: all 0.5s ease;

  &.recovery-mode {
    background: linear-gradient(135deg, rgba(255, 170, 68, 0.1) 0%, rgba(255, 136, 68, 0.08) 100%);
    border-color: rgba(255, 170, 68, 0.35);
    &::before {
      background: radial-gradient(circle, rgba(255, 170, 68, 0.1) 0%, transparent 70%);
    }
  }

  &::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle, rgba(68, 170, 68, 0.1) 0%, transparent 70%);
    animation: peace-glow 4s ease-in-out infinite;
  }

  .peace-icon-wrapper {
    position: relative;
    margin-bottom: 16px;

    .peace-icon {
      font-size: 48px;
      color: #66cc88;
      filter: drop-shadow(0 0 15px rgba(102, 204, 136, 0.5));
      animation: peace-float 3s ease-in-out infinite;
    }

    .peace-glow {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 80px;
      height: 80px;
      background: radial-gradient(circle, rgba(102, 204, 136, 0.3) 0%, transparent 70%);
      border-radius: 50%;
      animation: peace-pulse 2s ease-in-out infinite;
    }
  }

  .peace-title {
    font-size: 22px;
    font-weight: bold;
    color: #66cc88;
    text-shadow: 0 0 20px rgba(102, 204, 136, 0.5);
    margin-bottom: 8px;
    position: relative;
    z-index: 1;
    transition: color 0.3s ease;
  }

  .peace-subtitle {
    font-size: 13px;
    color: var(--text-secondary);
    font-style: italic;
    position: relative;
    z-index: 1;
  }
}

@keyframes peace-glow {
  0%, 100% { transform: translate(-50%, -50%) rotate(0deg); opacity: 0.5; }
  50% { transform: translate(-50%, -50%) rotate(180deg); opacity: 0.8; }
}
@keyframes peace-float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-8px); }
}
@keyframes peace-pulse {
  0%, 100% { transform: translate(-50%, -50%) scale(1); opacity: 0.5; }
  50% { transform: translate(-50%, -50%) scale(1.3); opacity: 0.2; }
}

// 恢复期图标变色
.peace-stat-icon.spirit-low {
  background: rgba(255, 170, 0, 0.15) !important;
  color: #ffaa00 !important;
}

.peace-stat-icon {
  &.injury-recovery-轻伤 { background: rgba(255, 204, 0, 0.15) !important; color: #ffcc00 !important; }
  &.injury-recovery-重伤 { background: rgba(255, 136, 0, 0.15) !important; color: #ff8800 !important; }
  &.injury-recovery-濒死 {
    background: rgba(255, 68, 68, 0.15) !important;
    color: #ff4444 !important;
    animation: pulse-danger 1.5s ease-in-out infinite;
  }
  &.tribulation-recovery { background: rgba(136, 68, 255, 0.15); color: #8844ff; }
  &.power { background: rgba(255, 170, 100, 0.15); color: #ffaa64; }
}

// 状态一览
.peace-stats {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 20px;

  .peace-stat-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 14px 16px;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 12px;

    .peace-stat-icon {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(74, 158, 255, 0.15);
      border-radius: 10px;
      color: #4a9eff;
      font-size: 18px;

      &.health {
        background: rgba(102, 204, 136, 0.15);
        color: #66cc88;
      }
    }

    .peace-stat-content {
      flex: 1;

      .peace-stat-label {
        font-size: 12px;
        color: var(--text-secondary);
        margin-bottom: 6px;
      }

      .peace-stat-bar {
        height: 8px;
        background: var(--progress-bg);
        border-radius: 4px;
        overflow: hidden;

        .peace-stat-fill {
          height: 100%;
          border-radius: 4px;
          transition: width 0.5s ease;

          &.spirit {
            background: linear-gradient(90deg, #4a9eff, #66bbff);
            box-shadow: 0 0 10px rgba(74, 158, 255, 0.5);
          }

          &.tribulation {
            border-radius: 4px;
            transition: width 0.5s ease;
            box-shadow: 0 0 10px rgba(136, 68, 255, 0.4);
          }
        }
      }

      .peace-stat-value {
        font-size: 13px;
        font-weight: 500;
        color: var(--text-accent);
        margin-top: 4px;
      }

      .combat-power-value {
        display: flex;
        align-items: center;
        gap: 4px;
        margin-top: 2px;

        .power-number {
          font-weight: bold;
          color: #ffaa64;
          text-shadow: 0 0 10px rgba(255, 170, 100, 0.4);
        }
      }

      .peace-stat-status {
        display: inline-flex;
        align-items: center;
        gap: 6px;
        padding: 4px 12px;
        border-radius: 12px;
        font-size: 13px;
        font-weight: 500;

        i { font-size: 12px; }

        &.status-无伤 { background: rgba(68, 170, 68, 0.15); color: #44aa44; }
        &.status-轻伤 { background: rgba(255, 204, 0, 0.15); color: #ffcc00; }
        &.status-重伤 { background: rgba(255, 136, 0, 0.15); color: #ff8800; }
        &.status-濒死 {
          background: rgba(255, 68, 68, 0.15);
          color: #ff4444;
          animation: pulse-danger 1.5s ease-in-out infinite;
        }
      }
    }
  }
}

// 恢复期已用手段
.recovery-used-section {
  margin-bottom: 16px;
  padding: 14px;
  background: linear-gradient(135deg, rgba(255, 200, 100, 0.08) 0%, rgba(255, 180, 80, 0.05) 100%);
  border: 1px dashed rgba(255, 200, 100, 0.3);
  border-radius: 12px;

  .recovery-used-header {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 10px;
    font-size: 13px;
    font-weight: 500;
    color: #ffc864;
    i { font-size: 14px; }
  }

  .recovery-used-list {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;

    .recovery-used-tag {
      padding: 5px 14px;
      background: rgba(255, 200, 100, 0.15);
      border: 1px solid rgba(255, 200, 100, 0.4);
      border-radius: 15px;
      font-size: 12px;
      color: #ffc864;
    }
  }
}

// 残余敌人
.recovery-enemies-section {
  margin-bottom: 16px;
  padding: 14px;
  background: var(--bg-secondary);
  border: 1px solid rgba(255, 100, 100, 0.25);
  border-radius: 12px;

  .recovery-enemies-header {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 10px;
    font-size: 13px;
    font-weight: 500;
    color: #ff8888;
    i { font-size: 14px; }

    .enemy-count {
      margin-left: auto;
      padding: 2px 10px;
      background: rgba(255, 100, 100, 0.15);
      border-radius: 10px;
      font-size: 12px;
      color: #ff6b6b;
    }
  }

  .recovery-enemies-list {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  .recovery-enemy-item {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 8px 12px;
    background: var(--button-bg);
    border-radius: 8px;
    font-size: 12px;

    .recovery-enemy-name { font-weight: 500; color: #ff8888; }

    .recovery-enemy-realm {
      padding: 2px 8px;
      background: var(--bg-secondary);
      border: 1px solid var(--border-color);
      border-radius: 8px;
      font-size: 10px;
      color: var(--text-accent);
    }

    .recovery-enemy-status {
      margin-left: auto;
      font-size: 11px;
      &.health-完好 { color: #44aa44; }
      &.health-轻伤 { color: #ffcc00; }
      &.health-重伤 { color: #ff8800; }
      &.health-濒死 { color: #ff4444; }
    }
  }
}

// 渡劫结果
.tribulation-result-section {
  margin-bottom: 16px;
  padding: 14px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 12px;

  .tribulation-result-header {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 10px;
    font-size: 13px;
    font-weight: 500;
    color: #8844ff;
    i { font-size: 14px; }
  }

  .tribulation-result-content {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    padding: 6px 16px;
    border-radius: 15px;
    font-size: 14px;
    font-weight: 600;

    &.result-成功 { background: rgba(68, 204, 136, 0.15); color: #44cc88; }
    &.result-失败 { background: rgba(255, 68, 68, 0.15); color: #ff6666; }
  }

  .tribulation-penalty {
    margin-top: 10px;
    padding: 10px 12px;
    background: rgba(255, 68, 68, 0.08);
    border: 1px dashed rgba(255, 68, 68, 0.3);
    border-radius: 8px;
    font-size: 12px;
    color: var(--text-secondary);
    line-height: 1.5;
  }
}

// 提示
.cultivation-tips {
  padding: 16px;
  background: linear-gradient(135deg, rgba(255, 200, 100, 0.08) 0%, rgba(255, 180, 80, 0.05) 100%);
  border: 1px dashed rgba(255, 200, 100, 0.3);
  border-radius: 12px;

  &.recovery-tips {
    border-color: rgba(255, 170, 100, 0.3);
    background: linear-gradient(135deg, rgba(255, 170, 100, 0.08) 0%, rgba(255, 136, 68, 0.05) 100%);
    .tip-header { color: #ffaa64; }
  }

  .tip-header {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 10px;
    font-size: 13px;
    font-weight: 500;
    color: #ffc864;
    i { font-size: 14px; }
  }

  .tip-content {
    font-size: 13px;
    color: var(--text-secondary);
    line-height: 1.6;
    font-style: italic;
  }
}

@keyframes pulse-danger {
  0%, 100% { box-shadow: 0 0 0 0 rgba(255, 68, 68, 0.4); }
  50% { box-shadow: 0 0 0 8px rgba(255, 68, 68, 0); }
}
