
.cultivation-status {
  width: 100%;
  margin: 0 auto;
  font-family: 'Microsoft YaHei', 'PingFang SC', sans-serif;
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  overflow: hidden;
  color: var(--text-primary);
  font-size: 14px;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  position: relative;

  // 背景图案层 - 优化祥云排布
  .pattern-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: var(--pattern-overlay);
    // 祥云图案尺寸和位置优化
    background-size:
      100px 65px,
      85px 55px,
      110px 70px,
      90px 60px,
      75px 50px;
    background-position:
      5% 15%,
      // 左上
      85% 25%,
      // 右上
      15% 65%,
      // 左下
      75% 70%,
      // 右下
      45% 85%; // 底部中央
    background-repeat: no-repeat;
    pointer-events: none;
    opacity: 0.25;
    z-index: 0;
  }

  // 仙鹤装饰 - 移到顶部中央
  .crane-decoration {
    position: absolute;
    top: 22px;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    height: 60px;
    z-index: 3;
    pointer-events: none;
    animation: crane-float 4s ease-in-out infinite;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      filter: drop-shadow(0 3px 12px rgba(0, 0, 0, 0.4));
    }
  }

  @keyframes crane-float {
    0%,
    100% {
      transform: translateX(-50%) translateY(0);
    }

    50% {
      transform: translateX(-50%) translateY(-8px);
    }
  }

  // 确保所有内容在图案层之上
  > * {
    position: relative;
    z-index: 1;
  }

  // 折叠状态
  &.collapsed {
    :deep(.tab-nav),
    .content {
      display: none;
    }

    :deep(.header) {
      border-bottom: none;
    }
  }
}

// 内容区域
.content {
  flex: 1;
  min-height: 280px;
  max-height: 600px;
  overflow-y: auto;

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: var(--bg-secondary);
    border-radius: 3px;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--button-bg);
    border-radius: 3px;

    &:hover {
      background: var(--button-hover);
    }
  }
}

.panel {
  padding: 16px;
}

// 手机端响应式适配
@media screen and (max-width: 480px) {
  .cultivation-status {
    font-size: 13px;
    border-radius: 8px;

    // 仙鹤装饰 - 手机端缩小
    .crane-decoration {
      width: 45px;
      height: 45px;
      top: 18px;
    }
  }

  // 内容区域适配
  .content {
    min-height: 220px;
    max-height: 450px;
  }

  .panel {
    padding: 12px;
  }
}

// 动画
@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
