
.companions-panel {
  padding: 16px;

  .companions-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .companion-card {
    padding: 12px;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 8px;

    .companion-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 8px;

      .companion-name {
        font-size: 15px;
        font-weight: bold;
        color: #ffb0c0;
      }

      .companion-realm {
        font-size: 12px;
      }
    }

    .companion-relation {
      font-size: 12px;
      color: var(--text-secondary);
      margin-bottom: 8px;

      .relation-value {
        color: var(--text-accent);
        margin-left: 4px;
      }
    }

    .companion-favor {
      display: flex;
      align-items: center;
      gap: 8px;

      .favor-label {
        font-size: 11px;
        color: var(--text-secondary);
        min-width: 42px;
      }

      .favor-bar {
        flex: 1;
        height: 6px;
        background: var(--progress-bg);
        border-radius: 3px;
        overflow: hidden;

        .favor-fill {
          height: 100%;
          border-radius: 3px;
          transition: width 0.3s ease;
        }
      }

      .favor-value {
        font-size: 12px;
        color: var(--text-accent);
        min-width: 30px;
        text-align: right;
      }
    }
  }

  .empty-hint {
    text-align: center;
    padding: 20px;
    color: #506070;
    font-style: italic;
  }
}

// 手机端适配
@media screen and (max-width: 480px) {
  .companions-panel {
    padding: 12px;

    .companion-card {
      padding: 10px;

      .companion-header {
        .companion-name {
          font-size: 14px;
        }

        .companion-realm {
          font-size: 11px;
        }
      }

      .companion-relation {
        font-size: 11px;
      }

      .companion-favor {
        .favor-label {
          font-size: 10px;
        }

        .favor-bar {
          height: 5px;
        }

        .favor-value {
          font-size: 11px;
        }
      }
    }
  }
}
