
.skills-panel {
  padding: 16px;

  .skills-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .skill-item {
    padding: 12px;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 8px;

    .skill-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 6px;
    }

    .skill-name {
      font-size: 14px;
      font-weight: bold;
      color: var(--accent-color);
    }

    .skill-rank {
      font-size: 11px;
      color: #ffc864;
      padding: 2px 8px;
      background: rgba(255, 200, 100, 0.1);
      border-radius: 4px;
    }

    .skill-desc {
      font-size: 12px;
      color: var(--text-secondary);
      line-height: 1.5;
      margin-bottom: 4px;
    }

    .skill-proficiency {
      font-size: 11px;
      color: var(--text-accent);
    }
  }

  .empty-hint {
    text-align: center;
    padding: 20px;
    color: #506070;
    font-style: italic;
  }
}

// 手机端适配
@media screen and (max-width: 480px) {
  .skills-panel {
    padding: 12px;

    .skills-list {
      gap: 10px;
    }

    .skill-item {
      padding: 10px;

      .skill-name {
        font-size: 13px;
      }

      .skill-rank {
        font-size: 10px;
      }

      .skill-desc {
        font-size: 11px;
      }

      .skill-proficiency {
        font-size: 10px;
      }
    }
  }
}
