
.footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 10px 16px;
  background: var(--footer-bg);
  border-top: 1px solid var(--border-color);

  .footer-item {
    display: flex;
    align-items: center;
    gap: 6px;
    font-size: 12px;
    color: var(--text-secondary);
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 6px 10px;
    border-radius: 6px;
    transition: all 0.2s;

    i {
      color: var(--accent-color);
      transition: transform 0.3s;
    }

    &:hover {
      background: var(--button-bg);
      color: var(--text-primary);

      i {
        transform: rotate(90deg);
      }
    }

    &:active {
      background: var(--button-active);
    }
  }

  .action-btn {
    font-weight: 500;
    position: relative;

    .action-badge {
      position: absolute;
      top: -4px;
      right: -4px;
      min-width: 16px;
      height: 16px;
      padding: 0 4px;
      background: var(--accent-color);
      color: #fff;
      font-size: 10px;
      font-weight: bold;
      border-radius: 8px;
      display: flex;
      align-items: center;
      justify-content: center;
      animation: badge-pulse 2s infinite;
    }
  }

  @keyframes badge-pulse {
    0%,
    100% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.1);
    }
  }

  .footer-center {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 6px;
    color: var(--text-accent);

    .moon-twinkle {
      animation: moon-twinkle 3s ease-in-out infinite;
    }
  }
}

@keyframes moon-twinkle {
  0%,
  100% {
    opacity: 1;
    filter: drop-shadow(0 0 2px var(--accent-color));
  }

  50% {
    opacity: 0.4;
    filter: drop-shadow(0 0 8px var(--accent-color));
  }
}

// 手机端适配
@media screen and (max-width: 480px) {
  .footer {
    padding: 8px 12px;

    .footer-item {
      font-size: 11px;
      gap: 4px;
      padding: 5px 8px;
    }

    .footer-center {
      font-size: 11px;
      gap: 4px;
    }
  }
}
