
// 战斗状态头部
.combat-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  margin-bottom: 16px;
  background: linear-gradient(135deg, rgba(255, 100, 100, 0.12) 0%, rgba(200, 80, 80, 0.08) 100%);
  border: 1px solid rgba(255, 100, 100, 0.35);
  border-radius: 12px;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 4px;
    background: linear-gradient(180deg, #ff6b6b 0%, #ff8888 50%, #ff6b6b 100%);
    border-radius: 4px 0 0 4px;
  }

  .combat-status-left {
    display: flex;
    align-items: center;
    gap: 12px;
  }

  .combat-status-indicator {
    width: 42px;
    height: 42px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    font-size: 18px;

    &.status-激战, &.status-战斗中 {
      background: rgba(255, 100, 100, 0.2);
      color: #ff6b6b;
    }
    &.status-对峙 {
      background: rgba(255, 180, 60, 0.2);
      color: #ffb43c;
    }
    &.status-重伤 {
      background: rgba(255, 136, 0, 0.2);
      color: #ff8800;
    }
    &.status-濒死 {
      background: rgba(200, 50, 50, 0.25);
      color: #cc3333;
    }

    .crossed-swords-icon {
      font-size: 20px;
      line-height: 1;
    }
  }

  .combat-status-text {
    display: flex;
    flex-direction: column;
    gap: 2px;
  }

  .combat-status-label {
    font-size: 17px;
    font-weight: bold;

    &.status-激战, &.status-战斗中 { color: #ff6b6b; }
    &.status-对峙 { color: #ffb43c; }
    &.status-重伤 { color: #ff8800; }
    &.status-濒死 { color: #cc3333; }
  }

  .combat-status-desc {
    font-size: 12px;
    color: var(--text-secondary);
    max-width: 180px;
  }

  .combat-round-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 8px 14px;
    background: rgba(255, 100, 100, 0.1);
    border: 1px solid rgba(255, 100, 100, 0.25);
    border-radius: 10px;

    .round-label {
      font-size: 10px;
      color: var(--text-secondary);
      text-transform: uppercase;
      letter-spacing: 1px;
    }

    .round-number {
      font-size: 20px;
      font-weight: bold;
      color: #ff6b6b;
      line-height: 1.1;
    }
  }
}

// 状态一览
.combat-stats {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 20px;

  .combat-stat-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 14px 16px;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 12px;

    .combat-stat-icon {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 10px;
      font-size: 18px;

      &.spirit {
        background: rgba(74, 158, 255, 0.15);
        color: #4a9eff;
      }
      &.power {
        background: rgba(255, 170, 100, 0.15);
        color: #ffaa64;
      }
      &.injury-icon-无伤 {
        background: rgba(102, 204, 136, 0.15);
        color: #66cc88;
      }
      &.injury-icon-轻伤 {
        background: rgba(255, 204, 0, 0.15);
        color: #ffcc00;
      }
      &.injury-icon-重伤 {
        background: rgba(255, 136, 0, 0.15);
        color: #ff8800;
      }
      &.injury-icon-濒死 {
        background: rgba(255, 68, 68, 0.15);
        color: #ff4444;
        animation: pulse-danger 1.5s ease-in-out infinite;
      }
    }

    .combat-stat-content {
      flex: 1;

      .combat-stat-label {
        font-size: 12px;
        color: var(--text-secondary);
        margin-bottom: 6px;
      }

      .combat-stat-bar {
        height: 8px;
        background: var(--progress-bg);
        border-radius: 4px;
        overflow: hidden;

        .combat-stat-fill {
          height: 100%;
          border-radius: 4px;
          transition: width 0.5s ease;

          &.spirit {
            box-shadow: 0 0 10px rgba(74, 158, 255, 0.5);
          }
        }
      }

      .combat-stat-value {
        font-size: 13px;
        font-weight: 500;
        color: var(--text-accent);
        margin-top: 4px;
      }

      .combat-power-value {
        display: flex;
        align-items: center;
        gap: 4px;
        margin-top: 2px;

        .power-number {
          font-size: 22px;
          font-weight: bold;
          color: #ffaa64;
          text-shadow: 0 0 10px rgba(255, 170, 100, 0.4);
        }
      }

      .combat-stat-status {
        display: inline-flex;
        align-items: center;
        gap: 6px;
        padding: 4px 12px;
        border-radius: 12px;
        font-size: 13px;
        font-weight: 500;

        i { font-size: 12px; }

        &.status-无伤 {
          background: rgba(68, 170, 68, 0.15);
          color: #44aa44;
        }
        &.status-轻伤 {
          background: rgba(255, 204, 0, 0.15);
          color: #ffcc00;
        }
        &.status-重伤 {
          background: rgba(255, 136, 0, 0.15);
          color: #ff8800;
        }
        &.status-濒死 {
          background: rgba(255, 68, 68, 0.15);
          color: #ff4444;
          animation: pulse-danger 1.5s ease-in-out infinite;
        }
      }
    }
  }
}

// 已用底牌
.trump-cards-section {
  margin-bottom: 20px;
  padding: 16px;
  background: linear-gradient(135deg, rgba(255, 200, 100, 0.08) 0%, rgba(255, 180, 80, 0.05) 100%);
  border: 1px dashed rgba(255, 200, 100, 0.3);
  border-radius: 12px;

  .trump-header {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 10px;
    font-size: 13px;
    font-weight: 500;
    color: #ffc864;
    i { font-size: 14px; }
  }

  .trump-cards-list {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;

    .trump-card-tag {
      padding: 5px 14px;
      background: rgba(255, 200, 100, 0.15);
      border: 1px solid rgba(255, 200, 100, 0.4);
      border-radius: 15px;
      font-size: 12px;
      color: #ffc864;
    }
  }
}

// 敌人区块
.enemies-section {
  .enemies-header {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 12px;
    font-size: 14px;
    font-weight: 500;
    color: var(--text-accent);

    i { font-size: 14px; color: #ff6b6b; }

    .enemy-count {
      margin-left: auto;
      padding: 2px 10px;
      background: rgba(255, 100, 100, 0.15);
      border-radius: 10px;
      font-size: 12px;
      color: #ff6b6b;
    }
  }

  .enemies-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .enemy-card {
    display: flex;
    align-items: flex-start;
    gap: 12px;
    padding: 14px;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 12px;
    transition: all 0.3s ease;

    &:hover { border-color: rgba(255, 100, 100, 0.4); }

    &.enemy-dead {
      opacity: 0.5;
      .enemy-name { text-decoration: line-through; color: var(--text-secondary); }
      .enemy-avatar { background: rgba(102, 102, 102, 0.15) !important; color: #666 !important; }
    }

    .enemy-avatar {
      width: 44px;
      height: 44px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 10px;
      font-size: 20px;
      flex-shrink: 0;

      &.threat-safe { background: rgba(68, 170, 68, 0.15); color: #44aa44; }
      &.threat-easy { background: rgba(136, 204, 68, 0.15); color: #88cc44; }
      &.threat-equal { background: rgba(255, 204, 0, 0.15); color: #ffcc00; }
      &.threat-hard { background: rgba(255, 136, 0, 0.15); color: #ff8800; }
      &.threat-deadly {
        background: rgba(255, 68, 68, 0.15);
        color: #ff4444;
        animation: pulse-danger 1.5s ease-in-out infinite;
      }
    }

    .enemy-info { flex: 1; min-width: 0; }

    .enemy-header-row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 10px;
      margin-bottom: 8px;

      .enemy-name { font-size: 15px; font-weight: 600; color: #ff8888; }

      .enemy-realm {
        padding: 2px 10px;
        background: var(--button-bg);
        border: 1px solid var(--border-color);
        border-radius: 10px;
        font-size: 11px;
        color: var(--text-accent);
        flex-shrink: 0;
      }
    }

    .enemy-status-row {
      display: flex;
      align-items: center;
      gap: 16px;
      margin-bottom: 6px;

      .enemy-power, .enemy-health {
        display: flex;
        align-items: center;
        gap: 6px;
        font-size: 12px;
        i { font-size: 11px; }
      }

      .enemy-power {
        &.power-safe { color: #44aa44; }
        &.power-easy { color: #88cc44; }
        &.power-equal { color: #ffcc00; }
        &.power-hard { color: #ff8800; }
        &.power-deadly { color: #ff4444; }

        .enemy-power-value { font-size: 10px; opacity: 0.8; margin-left: 2px; }
      }

      .enemy-health {
        &.health-完好 { color: #44aa44; }
        &.health-轻伤 { color: #ffcc00; }
        &.health-重伤 { color: #ff8800; }
        &.health-濒死 { color: #ff4444; }
        &.health-已死 { color: #666666; }
      }
    }

    .enemy-trait {
      display: flex;
      align-items: center;
      gap: 6px;
      padding-top: 8px;
      border-top: 1px dashed var(--border-color);
      font-size: 12px;
      color: var(--text-secondary);
      i { font-size: 10px; color: #ffc864; }
    }
  }

  .empty-hint {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 30px 20px;
    background: var(--bg-secondary);
    border: 1px dashed var(--border-color);
    border-radius: 12px;
    color: var(--text-secondary);

    i { font-size: 28px; color: #66aa88; }
    span { font-size: 13px; font-style: italic; }
  }
}

@keyframes pulse-danger {
  0%, 100% { box-shadow: 0 0 0 0 rgba(255, 68, 68, 0.4); }
  50% { box-shadow: 0 0 0 8px rgba(255, 68, 68, 0); }
}

// 手机端适配
@media screen and (max-width: 480px) {
  .trump-cards-section {
    .trump-cards-list {
      .trump-card-tag { padding: 5px 12px; font-size: 11px; }
    }
  }

  .enemies-section {
    .enemy-card {
      padding: 12px 12px 12px 16px;
      .enemy-header-row {
        .enemy-name { font-size: 13px; }
        .enemy-realm { padding: 2px 8px; font-size: 10px; }
      }
      .enemy-status-row {
        gap: 12px;
        .enemy-power, .enemy-health { font-size: 11px; i { font-size: 10px; } }
      }
      .enemy-trait { font-size: 10px; }
    }

    .empty-hint {
      padding: 24px 16px;
      i { font-size: 24px; }
      span { font-size: 12px; }
    }
  }
}
