
.section-header.tribulation-header {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 16px;
  padding-bottom: 12px;
  border-bottom: 1px solid var(--border-color);
  font-size: 15px;
  font-weight: bold;
  color: #8844ff;

  i {
    color: #8844ff;
    animation: lightning-flash 0.5s ease-in-out infinite;
  }
}

.tribulation-overview {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-bottom: 20px;
  padding: 20px;
  background: linear-gradient(135deg, rgba(136, 68, 255, 0.1) 0%, rgba(255, 68, 170, 0.1) 100%);
  border: 2px solid rgba(136, 68, 255, 0.4);
  border-radius: 12px;
  animation: tribulation-glow 2s ease-in-out infinite;

  .tribulation-type-badge {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 10px 20px;
    border-radius: 25px;
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    text-shadow: 0 2px 8px rgba(0, 0, 0, 0.5);
    animation: badge-pulse 1.5s ease-in-out infinite;

    i {
      font-size: 20px;
      animation: lightning-flash 0.5s ease-in-out infinite;
    }
  }

  .tribulation-level {
    font-size: 16px;
    font-weight: bold;
    padding: 8px 16px;
    background: var(--button-bg);
    border: 1px solid var(--border-color);
    border-radius: 20px;
  }
}

.tribulation-progress-section {
  margin-bottom: 20px;
  padding: 16px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 10px;

  .section-title {
    font-size: 13px;
    color: var(--text-accent);
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    gap: 6px;

    i {
      color: #8844ff;
    }
  }

  .tribulation-stages {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    margin-bottom: 12px;
    font-size: 16px;

    .stage-current {
      color: var(--accent-color);
      font-weight: bold;
    }

    .stage-divider {
      color: var(--text-secondary);
    }

    .stage-total {
      color: var(--text-secondary);
    }
  }

  .tribulation-progress-bar {
    height: 10px;
    background: var(--progress-bg);
    border-radius: 5px;
    overflow: hidden;

    .tribulation-progress-fill {
      height: 100%;
      border-radius: 5px;
      transition: width 0.5s ease;
      box-shadow: 0 0 15px currentColor;
    }
  }
}

.tribulation-endurance-section {
  margin-bottom: 16px;

  .section-title {
    font-size: 13px;
    color: var(--text-accent);
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    gap: 6px;

    i {
      color: #44aa44;
    }
  }

  .endurance-bar-container {
    display: flex;
    align-items: center;
    gap: 12px;

    .endurance-bar {
      flex: 1;
      height: 14px;
      background: var(--progress-bg);
      border-radius: 7px;
      overflow: hidden;

      .endurance-fill {
        height: 100%;
        border-radius: 7px;
        transition: width 0.5s ease, background-color 0.3s ease;
        box-shadow: 0 0 10px currentColor;
      }
    }

    .endurance-value {
      font-size: 15px;
      font-weight: bold;
      color: var(--text-primary);
      min-width: 55px;
      text-align: right;
    }
  }
}

.protection-section {
  margin-bottom: 16px;

  .section-title {
    font-size: 13px;
    color: var(--text-accent);
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    gap: 6px;

    i {
      color: #44aaff;
    }
  }

  .protection-list {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;

    .protection-tag {
      padding: 5px 14px;
      background: rgba(68, 170, 255, 0.15);
      border: 1px solid rgba(68, 170, 255, 0.4);
      border-radius: 15px;
      font-size: 12px;
      color: #44aaff;
    }
  }
}

.tribulation-desc-section {
  margin-bottom: 16px;
  padding: 14px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 10px;

  .section-title {
    font-size: 13px;
    color: var(--text-accent);
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    gap: 6px;

    i {
      color: #ffc864;
    }
  }

  .tribulation-desc {
    font-size: 13px;
    color: var(--text-secondary);
    line-height: 1.6;
    font-style: italic;
  }
}

.tribulation-cause-section {
  padding: 12px 14px;
  background: rgba(136, 68, 255, 0.1);
  border: 1px dashed rgba(136, 68, 255, 0.4);
  border-radius: 8px;
  display: flex;
  gap: 8px;
  align-items: flex-start;

  .cause-label {
    font-size: 12px;
    color: #8844ff;
    font-weight: 500;
    white-space: nowrap;
  }

  .cause-text {
    font-size: 12px;
    color: var(--text-secondary);
    line-height: 1.5;
  }
}

// 动画
@keyframes tribulation-glow {
  0%, 100% { box-shadow: 0 0 20px rgba(136, 68, 255, 0.3); }
  50% { box-shadow: 0 0 40px rgba(136, 68, 255, 0.6); }
}

@keyframes badge-pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

@keyframes lightning-flash {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

// 手机端适配
@media screen and (max-width: 480px) {
  .section-header.tribulation-header {
    font-size: 14px;
    margin-bottom: 12px;
    padding-bottom: 10px;
  }

  .tribulation-overview {
    padding: 16px;
    gap: 12px;

    .tribulation-type-badge {
      padding: 8px 14px;
      font-size: 15px;
      i { font-size: 16px; }
    }

    .tribulation-level {
      font-size: 14px;
      padding: 6px 12px;
    }
  }

  .tribulation-progress-section {
    padding: 12px;
    .section-title { font-size: 12px; }
    .tribulation-stages { font-size: 14px; }
    .tribulation-progress-bar { height: 8px; }
  }

  .tribulation-endurance-section {
    .section-title { font-size: 12px; }
    .endurance-bar-container {
      .endurance-bar { height: 12px; }
      .endurance-value { font-size: 13px; min-width: 50px; }
    }
  }

  .protection-section {
    .section-title { font-size: 12px; }
    .protection-list {
      .protection-tag { padding: 4px 10px; font-size: 11px; }
    }
  }

  .tribulation-desc-section {
    padding: 12px;
    .section-title { font-size: 12px; }
    .tribulation-desc { font-size: 12px; }
  }

  .tribulation-cause-section {
    padding: 10px 12px;
    .cause-label { font-size: 11px; }
    .cause-text { font-size: 11px; }
  }
}
