
.map-panel {
  padding: 16px;

  .current-location {
    text-align: center;
    padding: 20px;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 10px;
    margin-bottom: 16px;

    .location-name {
      font-size: 22px;
      font-weight: bold;
      color: #a0d0ff;
      margin-bottom: 4px;
    }

    .location-layer {
      font-size: 12px;
      color: var(--accent-color);
      margin-bottom: 10px;
    }

    .location-desc {
      font-size: 13px;
      color: var(--text-secondary);
      line-height: 1.5;
    }
  }

  .connections {
    margin-bottom: 20px;

    .connections-title {
      font-size: 13px;
      color: var(--text-secondary);
      margin-bottom: 10px;
    }

    .connections-list {
      display: flex;
      flex-wrap: wrap;
      gap: 8px;

      .connection-item {
        padding: 6px 12px;
        background: var(--bg-secondary);
        border: 1px solid;
        border-radius: 15px;
        font-size: 12px;
        color: var(--text-accent);
      }
    }
  }

  // 任务列表
  .tasks-section {
    margin-bottom: 20px;

    .section-title {
      font-size: 14px;
      font-weight: bold;
      color: var(--text-accent);
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      gap: 6px;

      i {
        color: var(--accent-color);
      }
    }

    .tasks-list {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }

    .task-item {
      padding: 12px;
      background: var(--bg-secondary);
      border: 1px solid var(--border-color);
      border-radius: 8px;
      border-left: 3px solid;

      &.task-进行中 {
        border-left-color: #4a9eff;
      }

      &.task-已完成 {
        border-left-color: #44aa44;
        opacity: 0.7;
      }

      &.task-已失败 {
        border-left-color: #ff4444;
        opacity: 0.7;
      }

      .task-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 6px;

        .task-name {
          font-size: 13px;
          font-weight: 500;
          color: var(--text-primary);
        }

        .task-type {
          font-size: 10px;
          padding: 2px 8px;
          border-radius: 10px;
          font-weight: 500;

          &.type-主线 {
            background: rgba(255, 100, 100, 0.2);
            color: #ff6464;
          }

          &.type-支线 {
            background: rgba(100, 150, 255, 0.2);
            color: #6496ff;
          }

          &.type-每日 {
            background: rgba(100, 200, 100, 0.2);
            color: #64c864;
          }

          &.type-临危受命 {
            background: rgba(255, 150, 50, 0.2);
            color: #ff9632;
          }
        }
      }

      .task-target {
        font-size: 12px;
        color: var(--text-secondary);
        margin-bottom: 4px;
        line-height: 1.4;
      }

      .task-status {
        font-size: 11px;
        color: var(--text-accent);
      }
    }
  }

  // 声望系统
  .reputation-section {
    .section-title {
      font-size: 14px;
      font-weight: bold;
      color: var(--text-accent);
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      gap: 6px;

      i {
        color: var(--accent-color);
      }
    }

    .reputation-list {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }

    .reputation-item {
      padding: 10px 12px;
      background: var(--bg-secondary);
      border: 1px solid var(--border-color);
      border-radius: 8px;

      .reputation-faction {
        font-size: 13px;
        font-weight: 500;
        color: var(--text-primary);
        margin-bottom: 6px;
      }

      .reputation-bar {
        height: 6px;
        background: var(--progress-bg);
        border-radius: 3px;
        overflow: hidden;
        margin-bottom: 6px;

        .reputation-fill {
          height: 100%;
          border-radius: 3px;
          transition: width 0.3s ease;
        }
      }

      .reputation-info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 11px;

        .reputation-value {
          color: var(--text-accent);
        }

        .reputation-relation {
          color: var(--text-secondary);
        }
      }
    }
  }

  .empty-hint {
    text-align: center;
    padding: 20px;
    color: #506070;
    font-style: italic;
  }
}

// 手机端适配
@media screen and (max-width: 480px) {
  .map-panel {
    padding: 12px;

    .current-location {
      padding: 16px;

      .location-name {
        font-size: 18px;
      }

      .location-layer {
        font-size: 11px;
      }

      .location-desc {
        font-size: 12px;
      }
    }

    .connections {
      .connections-title {
        font-size: 12px;
      }

      .connections-list {
        gap: 6px;

        .connection-item {
          padding: 5px 10px;
          font-size: 11px;
        }
      }
    }

    .tasks-section {
      .section-title {
        font-size: 13px;
      }

      .task-item {
        padding: 10px;

        .task-name {
          font-size: 12px;
        }

        .task-type {
          font-size: 9px;
          padding: 2px 6px;
        }

        .task-target {
          font-size: 11px;
        }

        .task-status {
          font-size: 10px;
        }
      }
    }

    .reputation-section {
      .section-title {
        font-size: 13px;
      }

      .reputation-item {
        padding: 8px 10px;

        .reputation-faction {
          font-size: 12px;
        }

        .reputation-bar {
          height: 5px;
        }

        .reputation-info {
          font-size: 10px;
        }
      }
    }
  }
}
