
.preset-panel {
  padding: 20px;
  overflow-y: auto;
  max-height: 100%;

  .preset-header {
    margin-bottom: 24px;
    padding-bottom: 16px;
    border-bottom: 2px solid var(--border-color);

    h3 {
      font-size: 20px;
      font-weight: 600;
      color: var(--text-accent);
      margin: 0 0 8px 0;
      display: flex;
      align-items: center;
      gap: 10px;

      i {
        color: var(--accent-color);
      }
    }

    .preset-desc {
      margin: 0;
      font-size: 13px;
      color: var(--text-secondary);
      opacity: 0.8;
    }
  }

  .preset-content {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-bottom: 24px;
  }

  .preset-section {
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 8px;
    padding: 16px;

    .section-title {
      display: flex;
      align-items: center;
      gap: 8px;
      font-size: 15px;
      font-weight: 600;
      color: var(--text-accent);
      margin-bottom: 16px;
      padding-bottom: 12px;
      border-bottom: 1px solid var(--border-color);

      i {
        color: var(--accent-color);
        font-size: 14px;
      }

      .btn-add-small {
        margin-left: auto;
        padding: 4px 10px;
        background: var(--accent-color);
        color: #2c1810;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        font-size: 12px;
        transition: all 0.2s;

        &:hover {
          background: #f4d03f;
          transform: scale(1.05);
        }
      }
    }

    .empty-hint {
      text-align: center;
      color: var(--text-secondary);
      font-size: 13px;
      padding: 20px;
      opacity: 0.7;
    }

    .item-card {
      background: var(--bg-primary);
      border: 1px solid var(--border-color);
      border-radius: 6px;
      padding: 12px;
      margin-bottom: 12px;

      &:last-child {
        margin-bottom: 0;
      }

      .item-header {
        display: flex;
        align-items: center;
        gap: 8px;
        margin-bottom: 10px;

        .item-name-input {
          flex: 1;
          padding: 6px 10px;
          background: var(--bg-secondary);
          border: 1px solid var(--border-color);
          border-radius: 4px;
          color: var(--text-accent);
          font-size: 14px;
          font-weight: 600;

          &:focus {
            outline: none;
            border-color: var(--accent-color);
          }
        }

        .btn-delete-small {
          padding: 4px 8px;
          background: rgba(220, 53, 69, 0.2);
          color: #dc3545;
          border: 1px solid rgba(220, 53, 69, 0.3);
          border-radius: 4px;
          cursor: pointer;
          font-size: 12px;
          transition: all 0.2s;

          &:hover {
            background: rgba(220, 53, 69, 0.4);
            border-color: #dc3545;
          }
        }
      }

      .item-row {
        display: flex;
        gap: 12px;
        margin-bottom: 8px;

        &:last-child {
          margin-bottom: 0;
        }

        &.full {
          flex-direction: column;
        }

        .item-field {
          flex: 1;
          display: flex;
          flex-direction: column;
          gap: 4px;

          &.full {
            width: 100%;
          }

          label {
            font-size: 11px;
            color: var(--text-secondary);
            text-transform: uppercase;
            letter-spacing: 0.5px;
          }

          input, select, textarea {
            padding: 6px 10px;
            background: var(--bg-secondary);
            border: 1px solid var(--border-color);
            border-radius: 4px;
            color: var(--text-primary);
            font-size: 13px;

            &:focus {
              outline: none;
              border-color: var(--accent-color);
            }
          }

          select {
            cursor: pointer;
          }

          textarea {
            resize: vertical;
            min-height: 40px;
            font-family: inherit;
          }
        }
      }
    }

    .form-row {
      display: flex;
      align-items: center;
      gap: 12px;
      margin-bottom: 12px;

      &:last-child {
        margin-bottom: 0;
      }

      label {
        flex: 0 0 100px;
        font-size: 13px;
        color: var(--text-secondary);
        text-align: right;
      }

      .input-with-hint {
        flex: 1;
        display: flex;
        align-items: center;
        gap: 12px;

        input {
          flex: 1;
          padding: 8px 12px;
          background: var(--bg-primary);
          border: 1px solid var(--border-color);
          border-radius: 6px;
          color: var(--text-primary);
          font-size: 13px;
          transition: all 0.2s;

          &:focus {
            outline: none;
            border-color: var(--accent-color);
            box-shadow: 0 0 0 2px rgba(255, 215, 0, 0.1);
          }

          &::placeholder {
            color: var(--text-secondary);
            opacity: 0.5;
          }
        }

        .realm-hint {
          flex: 0 0 auto;
          font-size: 13px;
          color: var(--accent-color);
          font-weight: 600;
          white-space: nowrap;
        }
      }

      input {
        flex: 1;
        padding: 8px 12px;
        background: var(--bg-primary);
        border: 1px solid var(--border-color);
        border-radius: 6px;
        color: var(--text-primary);
        font-size: 13px;
        transition: all 0.2s;

        &:focus {
          outline: none;
          border-color: var(--accent-color);
          box-shadow: 0 0 0 2px rgba(255, 215, 0, 0.1);
        }

        &::placeholder {
          color: var(--text-secondary);
          opacity: 0.5;
        }
      }
    }
  }

  .preset-actions {
    display: flex;
    gap: 12px;
    padding-top: 16px;
    border-top: 2px solid var(--border-color);

    &.secondary {
      padding-top: 12px;
      border-top: 1px solid var(--border-color);
      margin-top: 8px;
    }

    button {
      flex: 1;
      padding: 12px 20px;
      border: none;
      border-radius: 8px;
      font-size: 14px;
      font-weight: 600;
      cursor: pointer;
      transition: all 0.2s;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 8px;

      i {
        font-size: 14px;
      }
    }

    .btn-apply {
      background: linear-gradient(135deg, #d4af37 0%, #f4d03f 100%);
      color: #2c1810;
      box-shadow: 0 2px 8px rgba(212, 175, 55, 0.3);

      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 4px 12px rgba(212, 175, 55, 0.4);
      }

      &:active {
        transform: translateY(0);
      }
    }

    .btn-reset {
      background: var(--button-bg);
      color: var(--text-primary);
      border: 1px solid var(--border-color);

      &:hover {
        background: var(--button-hover);
        border-color: var(--accent-color);
      }

      &:active {
        background: var(--button-active);
      }
    }

    .btn-export {
      background: linear-gradient(135deg, #28a745 0%, #34ce57 100%);
      color: white;
      box-shadow: 0 2px 8px rgba(40, 167, 69, 0.3);

      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 4px 12px rgba(40, 167, 69, 0.4);
      }

      &:active {
        transform: translateY(0);
      }
    }

    .btn-import {
      background: linear-gradient(135deg, #17a2b8 0%, #20c997 100%);
      color: white;
      box-shadow: 0 2px 8px rgba(23, 162, 184, 0.3);

      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 4px 12px rgba(23, 162, 184, 0.4);
      }

      &:active {
        transform: translateY(0);
      }
    }
  }
}

// 滚动条样式
.preset-panel {
  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: var(--bg-secondary);
    border-radius: 3px;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--button-bg);
    border-radius: 3px;

    &:hover {
      background: var(--button-hover);
    }
  }
}

// 红颜选择区域样式
.section-hint {
  font-size: 12px;
  color: var(--text-secondary);
  margin-bottom: 12px;
  opacity: 0.8;
}

.companion-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 10px;
  margin-bottom: 16px;
}

.companion-option {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  padding: 10px 12px;
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    border-color: var(--accent-color);
    background: rgba(255, 215, 0, 0.05);
  }

  &.selected {
    border-color: var(--accent-color);
    background: rgba(255, 215, 0, 0.1);

    .companion-checkbox {
      background: var(--accent-color);
      border-color: var(--accent-color);
      color: #2c1810;
    }
  }

  .companion-checkbox {
    flex: 0 0 20px;
    width: 20px;
    height: 20px;
    border: 2px solid var(--border-color);
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 11px;
    transition: all 0.2s;
  }

  .companion-info {
    flex: 1;
    min-width: 0;

    .companion-name {
      font-size: 14px;
      font-weight: 600;
      color: var(--text-accent);
      margin-bottom: 4px;
    }

    .companion-meta {
      display: flex;
      gap: 8px;
      margin-bottom: 2px;

      .companion-realm {
        font-size: 11px;
        color: var(--text-secondary);
      }

    }

    .companion-attr {
      font-size: 11px;
      color: var(--text-secondary);
      opacity: 0.8;
    }
  }
}

.selected-companions {
  border-top: 1px solid var(--border-color);
  padding-top: 16px;

  .selected-title {
    font-size: 13px;
    font-weight: 600;
    color: var(--text-accent);
    margin-bottom: 12px;
  }
}

.companion-card {
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  margin-bottom: 12px;
  overflow: hidden;

  &:last-child {
    margin-bottom: 0;
  }

  .companion-card-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 12px;
    background: rgba(255, 215, 0, 0.05);
    border-bottom: 1px solid var(--border-color);

    .companion-card-name {
      font-size: 14px;
      font-weight: 600;
      color: var(--text-accent);
    }
  }

  .companion-card-body {
    padding: 12px;

    .form-row {
      margin-bottom: 12px;

      &:last-child {
        margin-bottom: 0;
      }

      label {
        flex: 0 0 80px;
      }

      .favor-input-group {
        flex: 1;
        display: flex;
        align-items: center;
        gap: 12px;

        .favor-slider {
          flex: 1;
          height: 6px;
          appearance: none;
          background: linear-gradient(to right, #dc3545 0%, #ffc107 50%, #28a745 100%);
          border-radius: 3px;
          cursor: pointer;

          &::-webkit-slider-thumb {
            appearance: none;
            width: 16px;
            height: 16px;
            border-radius: 50%;
            background: var(--accent-color);
            border: 2px solid #2c1810;
            cursor: pointer;
          }
        }

        .favor-value {
          flex: 0 0 40px;
          text-align: center;
          font-weight: 600;
          font-size: 14px;

          &.favor-high {
            color: #28a745;
          }

          &.favor-good {
            color: #6bcb77;
          }

          &.favor-neutral {
            color: var(--text-secondary);
          }

          &.favor-low {
            color: #ffc107;
          }

          &.favor-hostile {
            color: #dc3545;
          }
        }
      }

      .relation-select {
        flex: 1;
        padding: 8px 12px;
        background: var(--bg-secondary);
        border: 1px solid var(--border-color);
        border-radius: 6px;
        color: var(--text-primary);
        font-size: 13px;
        cursor: pointer;

        &:focus {
          outline: none;
          border-color: var(--accent-color);
        }
      }
    }
  }
}

// 手机端响应式
@media screen and (max-width: 480px) {
  .preset-panel {
    padding: 16px;

    .preset-header h3 {
      font-size: 18px;
    }

    .preset-section {
      padding: 12px;

      .item-card {
        padding: 10px;

        .item-row {
          flex-direction: column;
          gap: 8px;
        }
      }

      .form-row {
        label {
          flex: 0 0 80px;
          font-size: 12px;
        }

        input {
          font-size: 12px;
          padding: 6px 10px;
        }
      }
    }

    .companion-grid {
      grid-template-columns: 1fr;
    }

    .preset-actions {
      flex-direction: column;

      button {
        padding: 10px 16px;
        font-size: 13px;
      }
    }
  }
}
